import Vue from 'vue';
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    mealsArray: [],
    selectedMeal: {
      mealName: "",
      mealRecipeInstructions: "",
      //mealRecipeInstructionsLines: ["step 1", "step 2", "step 3"],
      mealIngredients: []
    },
    showResults: false,
    searchTerm: ""
  },
  getters: {
    checkForMealFound: state => {
      if(state.showResults) {
        return true;
      } else {
        return false;
      }
    }
  },
  mutations: {
    hideMealDetails: state => {
      state.showResults = false;
    },
    showMealDetails: state => {
      state.showResults = true;
    },
    addMeals: (state, newMeal) => {
      state.mealsArray = newMeal;
    },
    updateSelectedMeal: (state, selectedMeal) => {
      state.selectedMeal.mealName = selectedMeal.strMeal;
      state.selectedMeal.mealRecipeInstructions = selectedMeal.strInstructions;    
    },
    clearSelectedMeal: state => {
      state.selectedMeal.mealName = "";
    },
    updateSearchTerm: (state, newSearchTerm) => {
      state.searchTerm = newSearchTerm;
    },
    updateIngredientsArray: (state, ingredient) => {
      state.selectedMeal.mealIngredients.push(ingredient);
    },
    clearIngredientsArray: state => {
      while (state.selectedMeal.mealIngredients.length) {
        state.selectedMeal.mealIngredients.pop();
      }  
    }
  }
});