<template>

    <!-- Page Body Content -->
    <section class="py-5" v-if="isMealFound">
      <div class="container">
          <h2 class="font-weight-light">Meals found</h2>
          <p>We have found the following meals which include the search term <strong>{{ currentSearchTerm }}</strong> </p>

          <div class="meal-images-container">
            <div class="meal-images card mb-2 mr-2" v-for="(meal,index) in meals" :key="meal.strMeal">
                <img class="card-img-top" v-bind:src='meal.strMealThumb'/>
                <div class="card-body card-text">
                  <button class="btn btn-outline-success" @click="getSelectedMeal(index)">{{meal.strMeal}}</button>
                </div>
            </div>  
          </div>

          <div v-if="this.$store.state.selectedMeal.mealName" class="mt-5">

            <h4 class="alert alert-success text-center">{{this.$store.state.selectedMeal.mealName}}</h4>

            <h4 class="mt-4">Ingredients:</h4> 
            <div v-for="(ingredient, index) in this.$store.state.selectedMeal.mealIngredients" :key="index">
              {{ingredient.name}} ({{ingredient.measure}})
            </div>

            <h4 class="mt-4">Instructions:</h4>
            <p>{{this.$store.state.selectedMeal.mealRecipeInstructions}}</p>

          </div>

      </div>
    </section>  
</template>

<script>
  export default {
    name: 'Body',
    props: {
    },
    data: function() {
      return {      
      }
    },
    mounted: function() {
    },
    methods: {
      getSelectedMeal(mealIndex) {
        this.$store.commit('updateSelectedMeal', this.$store.state.mealsArray[mealIndex]); 

        this.$store.commit('clearIngredientsArray');
        for(var propIndex = 1; propIndex <= 20; propIndex++) {
          var strIngredient = 'strIngredient' + propIndex;
          var strMeasure = 'strMeasure' + propIndex
          if(this.$store.state.mealsArray[mealIndex][strIngredient]) {
            this.$store.commit('updateIngredientsArray', 
              {
                name: this.$store.state.mealsArray[mealIndex][strIngredient], 
                measure: this.$store.state.mealsArray[mealIndex][strMeasure]
              });            
          }
        }

      }
    },
    computed: {
      meals(){
        return this.$store.state.mealsArray;
      },     
      isMealFound() {
        return this.$store.getters.checkForMealFound;
      },
      currentSearchTerm() {
        return this.$store.state.searchTerm;
      }
    }
  }
</script>

<style scoped>

  .meal-images-container {
    display: flex;
    flex-wrap: wrap;
  }

  .meal-images img {
    height: 200px;
    width: 200px;
    /* margin-right: 5px;
    margin-bottom: 5px; */
  }

  .card-text {
    width: 200px;
    word-wrap: wrap;
    text-align: center;
  }

  .card-body {
        background-color: whitesmoke;
        color: black;
  }

</style>